.container{
  display: flex;
  /* border: 2px solid red; */
  justify-content: center;
  align-items: center;
  align-self: center;
  height: 100vh;
  background-color: #8EC5FC;
  background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);
}
 
.App{
/* border: 2px solid blue; */
display: flex;
flex-direction: column;
align-items: center;
/* justify-content: center; */
width:55%;
height:auto;
color:rgb(0, 0, 0);
background: rgba(253, 253, 253, 0.384);
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 10.5px );
-webkit-backdrop-filter: blur( 10.5px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
font-size: 1.2rem;
margin-bottom: auto;
margin-top:auto;
}


.add-todo{
  width:500px;
  height:40px;
  display: flex;
  border-radius: 10px;
  margin-bottom: 30px;
  
}
.input-todo{
  border:none;
  width:70%;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  
}
input.input-todo {
  box-sizing: border-box;
  width: 100%;
  text-indent: 20px;
  font-size: 15px;
}
::placeholder{
  padding-left: 8px;
  font-size: 15px;
}
.btn{
  width:30%;
  border:none;
  background-color: rgb(0, 0, 0);
  color:white;
  border-top-right-radius: 20px; 
  border-bottom-right-radius: 20px; 
}

.btn:hover {
  background-color: #29bfff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.add-todo .btn:hover {
  background-color: rgb(134, 35, 204);
  box-shadow: 0 5px 8px rgba(233, 50, 123, 0.6);
}

/* Existing styles */

.completed {
  background-color: rgb(0, 0, 0);
  text-decoration: line-through;
  opacity: 0.7;
}

.complete-btn {
  background-color: #00cc00;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 0.5rem;
}

.complete-btn:hover {
  background-color: #a7f7a7;
}

/* New styles for completed tasks */
.completed-tasks {
  margin-top: 1rem;
}

.completed-task {
  background-color: #8eed96;
  padding: 0.5rem;
  margin: 0.5rem 0;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.timestamp {
  font-size: 0.8rem;
  color: #027c00;
  margin-left: 5px;
}
/* Rest of your existing CSS styles */

.completed {
  background-color: rgb(68, 189, 68);
  text-decoration: line-through;
  opacity: 0.7;
  margin-left: 5px;
}

