@media only screen and (max-width: 600px) {
  .App {
    width:80%;
    font-size: 1rem;
    margin-bottom: 650px;
    text-align: center;
  }
  .add-todo{
    width:80%;
    margin-bottom: 10px;
  }
  .input-todo{
     width:auto
  }
}